import { useState, useEffect, useCallback } from 'react';
import axios from 'src/utils/axios';

import ContentWrapper from 'src/components/ContentWrapper';
import PageHeader from './PageHeader';
import Footer from 'src/components/Footer';
import PageTitleWrapper from 'src/components/PageTitleWrapper';
import {getDocumentacionByClub,getDocumentacionCategorias} from 'src/api/documentacion'
import { Container, Grid , Box} from '@material-ui/core';
import useRefMounted from 'src/hooks/useRefMounted';
import useAuth from 'src/hooks/useAuth';

import Results from './Results';

function ManagementProjects() {
  const { user } = useAuth();

  const token = window.localStorage.getItem('accessToken');


  const isMountedRef = useRefMounted();
  const [projects, setProjects] = useState([]);
  const [solicitudes,setSolicitudes] = useState([])


  const getProjects = useCallback(async () => {
    try {
      const response = await axios.get('/api/projects');

      if (isMountedRef.current) {
        setProjects(response.data.projects);
      }
    } catch (err) {
      console.error(err);
    }
  }, [isMountedRef]);

const llamadaDocumentacion = async(club) => {
  const result = await getDocumentacionByClub(club,token)
  setSolicitudes(result)
  console.log('resultao llamada',result)
}



  useEffect(()=>{

    if(user){
      llamadaDocumentacion(user.clubId)

    }


  },[])


  useEffect(() => {
    getProjects();
  }, [getProjects]);

  return (
    <ContentWrapper title="Documentación">
      <PageTitleWrapper>
        <PageHeader />
      </PageTitleWrapper>
      <Container maxWidth="lg">
        <Grid
          container
          direction="row"
          justifyContent="center"
          alignItems="stretch"
          spacing={3}
        >
          <Grid item xs={12}>
            <Results projects={solicitudes}  />
          </Grid>
        </Grid>
        <Box m={5} /> 
      </Container>

    </ContentWrapper>
  );
}

export default ManagementProjects;
